import React from 'react';
import ContenusArticles from './ContenusArticles'
import {Link} from 'react-router-dom'
import Alert from 'react-bootstrap/Alert'
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'

const AfficherParagraphe=(nom )=>{
  const article =ContenusArticles.find((article) => article.nom===nom);
  {article.contenu.map((paragraphe,index) =>

    <p key={index}> {paragraphe}</p>
    )}
}
function PageListeArticles(){

    return(

       <>
          <div >
          <Alert variant="success" className="mg-t:5px">
                <Alert.Heading>Bienvenue pour vous tous </Alert.Heading>
                <p>
                  Vous trouvez ci joints une liste des cours pour ce semaine 
                </p>
                <hr />
                <p className="mb-0">
                  Je serais à votre disposition pour tous questions 
                </p>
        </Alert>
          </div>
    

     
    <h1 className="mb-0">Liste des articles </h1>

         {
             ContenusArticles.map((article,index) =>
             <Link to={`/article/${article.nom}`}>
                <h3>{article.titre}</h3>
             </Link>
             
             )
         }
 
    {
      ContenusArticles.map((article,index) =>
     
      <Card>
      <Card.Header>{article.nom}</Card.Header>
      <Card.Body>
    <Card.Title>{article.titre}</Card.Title>
    {article.contenu.map((paragraphe,index) =>

           <p key={index}> {paragraphe}</p>
     )}
        <Card.Text>
          {article.contenu}
        </Card.Text>
        <Button variant="primary" onClick={ () => AfficherParagraphe(article.nom)}>Plus d'informations</Button>
      </Card.Body>
      </Card>
        )
      }

       </>

    );
}


export default PageListeArticles;