import React from 'react'
import {LinkContainer} from 'react-router-bootstrap'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import NavDropdown from 'react-bootstrap/NavDropdown'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import FormControl from 'react-bootstrap/FormControl'

function BareNavigation(){

    return (

<Navbar bg="dark" expand="sm" variant="dark" className="mg-t:20px">
           
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
                <LinkContainer to="/" exact>
                <Nav.Link >Accueil</Nav.Link>
                </LinkContainer>
                <LinkContainer to="/a-propos">
                <Nav.Link >À propos </Nav.Link>
                </LinkContainer>
                <LinkContainer to="/liste-articles">
                <Nav.Link >Articles </Nav.Link>
                </LinkContainer>
                <NavDropdown title="Projets" id="basic-nav-dropdown">
                    <LinkContainer to="/jeu_de_dragon">
                       <NavDropdown.Item >Jeu de dragons  </NavDropdown.Item>
                    </LinkContainer>
                    <LinkContainer to="/jeu-main-de">
                       <NavDropdown.Item >Jeu de des </NavDropdown.Item>
                    </LinkContainer>
                    
                    <LinkContainer to="/etat-ampoule">
                       <NavDropdown.Item >État ampoule</NavDropdown.Item>
                    </LinkContainer>
               </NavDropdown>
           </Nav>

           <Form inline>
                <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                <Button variant="outline-info">Search</Button>
          </Form>
          
    </Navbar.Collapse>
</Navbar>
    )
}

export default BareNavigation;