import { React,useState,useEffect} from 'react';
import ContenusArticles from './ContenusArticles'
import Alert from 'react-bootstrap/Alert'
import Button from 'react-bootstrap/Button'
import Toast from 'react-bootstrap/Toast'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
function PageArticle({match}){
const nom=match.params.nom;
const article =ContenusArticles.find((article) => article.nom===nom);
const [infoArticle,setInfoArticle]=useState({votes:0,commentaires:[]});
const [show, setShow] = useState(false);

useEffect(()=>{
    const chercherDonnes=async()=>{
        const resultat = await fetch(`/api/articles/${nom} `); 
        const body =await resultat.json();
        setInfoArticle(body);
    }
    chercherDonnes();
},[nom]);

function AlertDismissible() {
    const [show, setShow] = useState(true);
  
    return (
      <>
      <div className="">
        <Alert show={show} variant="success">
          <Alert.Heading>How's it going?!</Alert.Heading>
          <p>
          Cet article a été apprécié {infoArticle.votes}  fois
          </p>
          <hr />
          <div className="d-flex justify-content-end">
            <Button onClick={() => setShow(false)} variant="outline-success">
              Fermer
            </Button>
          </div>
        </Alert>
  
        {!show && <Button onClick={() => setShow(true)}>Afficher Alert</Button>}
        </div>
      </>
    );
  }
  // Si l'article est vide   
if(!article){

    return(

        <h1>Article non trouvé </h1>
    ) 
}
    return(

       <>
<div className="mt-5">

   <p> <AlertDismissible/></p>
    <Alert variant="info">
        <Alert.Heading>Hey, nice to see you</Alert.Heading>
        <p>
            {article.titre}
        </p>
      
    </Alert>
</div>
         {article.contenu.map((paragraphe,index) =>

         <p key={index}> {paragraphe}</p>

         
         )}
 <div className=" mt-2">
     <Alert variant="dark">
       <h5>Commentaitres: </h5>
     </Alert>
      <Toast>
        <Toast.Header>
        <img src="holder.js/20x20?text=%20" className="rounded mr-2" alt="" />
        <strong className="mr-auto">Sidi Jdide</strong>
        <small>just now</small>
        </Toast.Header>
        
        <Toast.Body>Waw </Toast.Body>
    
      </Toast>
        <Toast>
        <Toast.Header>
        <img src="holder.js/20x20?text=%20" className="rounded mr-2" alt="" />
        <strong className="mr-auto">Samuel major </strong>
        <small>2 seconds ago</small>
        </Toast.Header>
        <Toast.Body>Excellent </Toast.Body>
       </Toast>
    </div>
</>

    );
}


export default PageArticle;